 import Vue from 'vue'
 /**
 * getUuid id
 */
function getUuid() {
	return Number(Math.random().toString().substr(3, 3) + Date.now()*Math.random()).toString(36)
}

/**
 * getColor 
 */
function getColor() {
	let colorArr = ['yellow','orange','red','pink','mauve','purple','blue','cyan','green','olive','grey','brown'];
	return colorArr[Math.floor(Math.random() * colorArr.length)]
}

/**
 * showTips 
 */
function showTips(msg) {
	uni.showToast({
		title: msg,
		icon: 'none'
	})
}

/**
 * copyText 
 */
function copyText(text) {

	uni.setClipboardData({
		data: text,
		success: function() {
			showTips('')
		},
		fail: function() {
			showTips('')
		},
	});











}
const util={
	getUuid,
	getColor,
	showTips,
	copyText
}
export default util

Vue.prototype.$util = util;